import React from "react";


class ProjectCreateForm extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            project_name: '',
            users: [],
            repository_link: ''
        }
    }

    handleChange(event) {

        this.setState({
            [event.target.name]: event.target.value
        });

        console.log(event.target.value)
    }

    handleSubmit(event) {
        this.props.createProject(this.state.project_name, this.state.users, this.state.repository_link)
        console.log(this.state.project_name)
        console.log(this.state.users)
        console.log(this.state.repository_link)
        event.preventDefault()
    }

    render() {
        return (
            <form className="col-6 mt-5 form-group" onSubmit={(event) => this.handleSubmit(event)}>

                <label htmlFor="text">Название проекта</label>
                <input type="text"
                       className="form-control"
                       name="project_name"
                       value={this.state.projectName}
                       onChange={(event) => this.handleChange(event)}/>

                <label htmlFor="text">Ссылка на репозиторий</label>
                <input type="text"
                       className="form-control"
                       name="repository_link"
                       value={this.state.repositoryLink}
                       onChange={(event) => this.handleChange(event)}/>

                <label htmlFor="text">Создатель</label>
                <input type="number"
                       className="form-control"
                       name="users"
                       value={this.state.users}
                       onChange={(event) => this.handleChange(event)}/>


                <input type="submit" className="btn btn-primary mt-2 col-4" value="Сохранить"/>
            </form>
        )
            ;

    }

}

export default ProjectCreateForm