import React from "react";


class ToDosCreateForm extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            project: '',
            content_creator: [],
            description: ''
        }
    }

    handleChange(event) {

        this.setState({
            [event.target.name]: event.target.value
        });

        console.log(event.target.value)
    }

    handleSubmit(event) {
        this.props.createTodo(this.state.project, this.state.content_creator, this.state.description)
        console.log(this.state.project)
        console.log(this.state.content_creator)
        console.log(this.state.state)
        event.preventDefault()
    }

    render() {
        return (
            <form className="col-6 mt-5 form-group" onSubmit={(event) => this.handleSubmit(event)}>

                <label htmlFor="text">Название проекта</label>
                <input type="number"
                       className="form-control"
                       name="project"
                       value={this.state.project}
                       onChange={(event) => this.handleChange(event)}/>

                <label htmlFor="text">Создатель</label>
                <input type="number"
                       className="form-control"
                       name="content_creator"
                       value={this.state.content_creator}
                       onChange={(event) => this.handleChange(event)}/>

                <label htmlFor="text">Описание</label>
                <input type="text"
                       className="form-control"
                       name="description"
                       value={this.state.description}
                       onChange={(event) => this.handleChange(event)}/>


                <input type="submit" className="btn btn-primary mt-2 col-4" value="Сохранить"/>
            </form>
        )
            ;

    }

}

export default ToDosCreateForm