import React from "react";


class SearchProjects extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            project_name: '',
        }
    }

    render() {
        return (
            <form className="col-6 mt-5 form-group" onSubmit={(event) => this.handleSubmit(event)}>

                <label htmlFor="text">Введите название проекта</label>
                <input type="text"
                       className="form-control"
                       name="search"
                       value=""
                       onChange="" />

                <input type="submit" className="btn btn-primary mt-2 col-4" value="Поиск"/>
            </form>
        )
    }
}


export default SearchProjects